import {
    ADD_CONTACT,
    CHANGE_CONTACTS, GET_CHOSEN_CONTACT,
    GET_USERS,
    INITIALIZE_CONTACTS,
    SEARCH_USERS, SET_CHOSEN_CONTACT,
    SET_CHOSEN_CONTACT_ID
} from "../../utils/constants";

export const defaultContactsState = {

    // contacts: [
    // 	{
    // 		logo: "data:image/png;base64,",
    // 		name: "",
    // 		text: "  ",
    // 		time: "19:00",
    // 		active: false
    // 	},
    // 	{
    // 		logo: "data:image/jpeg;base64,",
    // 		name: "",
    // 		text: "",
    // 		time: "10:00",
    // 		active: false
    // 	}
    // ]

    contacts: [],
    searchedContacts: [],
    chosenContact: undefined,
    chosenContactId: undefined
};


export const contactsReducer = (state = defaultContactsState, action) => {
    switch (action.type) {
        case ADD_CONTACT:
            return {...state, contacts: [...state.contacts, state.contacts.push(action.payload)]};
        case CHANGE_CONTACTS:
            return {...state, contacts: action.payload};
        case SEARCH_USERS:
            return {
                ...state, searchedContacts: action.payload
            };
        case INITIALIZE_CONTACTS:
            return {...state, searchedContacts: state.contacts}
        case SET_CHOSEN_CONTACT:
            return {...state, chosenContact: action.payload};
        case SET_CHOSEN_CONTACT_ID:
            return {...state, chosenContactId: action.payload};
        default:
            return state;
    }

}

    export const addContactAction = (payload) => {
        return {type: ADD_CONTACT, payload};
    };

    export const changeContactsAction = (payload) => {
        return {type: CHANGE_CONTACTS, payload};
    };

    export const getUsersAction = () => ({type: GET_USERS});

    export const searchContactsAction = (payload) => ({type: SEARCH_USERS, payload});

    export const initializeSearchedContactsAction = () => ({type: INITIALIZE_CONTACTS});

    export const getChosenContact = (payload) => ({type: GET_CHOSEN_CONTACT, payload});

    export const setChosenContact = (payload) => ({type: SET_CHOSEN_CONTACT, payload});

    export const setChosenContactIdAction = (payload) => ({type: SET_CHOSEN_CONTACT_ID, payload});